@import url(https://fonts.googleapis.com/css?family=Montserrat:700|Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body {
	font-size: .75em;
	background-image: url(/static/media/tictac.a2e9b7c1.png);
	color: rgba(39, 37, 28, 0.877);
	letter-spacing: 5px;
	overflow-x: hidden;
	z-index: 1;
}

.App {
	text-align: center;
}

.App-header {
	position: relative;
	min-height: 60vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	color: black;
	top: 4em;
}
.heading-background {
	position: fixed;
	opacity: 0.05;
	font-size: 25em;
	left: -0.2em;
	top: -0.5em;
	z-index: -100;
	color: #9352ae;
}
header {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}
header h1 {
	font-size: 1.5em;
	font-weight: 400;
	padding: 2em;
	margin-right: 50em;
}
nav {
	position: fixed;
	left: 60em;
	top: 6em;
	z-index: 100;
}
nav li {
	display: inline-block;
	cursor: pointer;
}
nav li .link {
  font-size: 1.5em;
	text-decoration: none;
	color: black;
	padding: 1em;
	opacity: 0.5;
	transition: all 0.3s ease-in-out;
	-webkit-transition: all 0.3s ease-in-out;
	-moz-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
}
li .link:hover {
	opacity: 1;
}
.active {
	color: black;
	opacity: 1;
}

.header-title button {
	background: black;
	border: 0px;
	padding: 1em 3em;
	transition: all 0.2s ease-out;
	-webkit-transition: all 0.2s ease-out;
	-moz-transition: all 0.2s ease-out;
	-ms-transition: all 0.2s ease-out;
	-o-transition: all 0.2s ease-out;
}
.header-title button:hover {
	opacity: 0.8;
}
.header-title button a {
	font-family: "Open Sans", sans-serif;
	color: white;
	text-decoration: none;
}
.header-title {
	position: relative;
	font-family: "Montserrat", sans-serif;
	font-size: 3em;
	text-align: left;
	top: 2em;
}
.about {
	display: flex;
	flex-direction: row;
}
.about-content {
	position: relative;
	left: 15em;
}
.about-content h1 {
	font-family: "Montserrat", sans-serif;
	font-size: 5em;
	text-align: left;
}
.about-content p {
	font-size: 2em;
	text-align: left;
	width: 37em;
}
.about img {
	margin-left: 5em;
	border: 0.1em solid black;
	height: 50em;
	width: auto;
}
.work {
	position: relative;
	display: flex;
	flex-direction: column;
	left: 15em;
	top: 8em;
	align-items: flex-start;
}
.work .heading {
	font-family: "Montserrat", sans-serif;
	font-size: 5em;
	text-align: left;
}
.work .work-content {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 2em;
}

.project img {
  border: 0.1em solid black;
	width: auto;
	height: 50em;
}
.contact {
	position: relative;
	top: 20em;
	left: 15em;
	text-align: left;
}
.contact h1 {
	font-family: "Montserrat", sans-serif;
	font-size: 5em;
	text-align: left;
}
.amazing-color {
	color: #8be0e8;
	opacity: 1;
}

.contact .email {
	color: black;
	font-size: 2em;
}
.contact li {
	display: inline-block;
	margin-top: 0;
	margin-bottom: 0;
	margin-right: 1em;
	margin-left: 0;
}
.contact li a {
	text-decoration: none;
	color: black;
}
.footer {
	position: relative;
	top: 2em;
	padding: 1em;
}
.footer a {
	text-decoration: none;
	color: #2c59fb;
}

/* SMARTPHONES PORTRAIT */
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
	body {
		font-size: 14px;
  }
  
  .App-header {
	position: relative;
	min-height: 100em;
	top: 4em;
}
	header h1 {
		font-size: 3em;
		padding: 1em;
		margin-right: 0em;
	}

	.heading-background {
		display: none;
	}
	nav {
		display: none;
	}
	.header-title {
		font-size: 3em;
		text-align: left;
		top: 1em;
		left: 1em;
	}
	.about {
		position: relative;
		display: flex;
		top: -50em;
	}
	.about img {
		display: none;
	}
	.about-content {
		left: 2em;
	}
	.about-content .contact p {
		font-size: 3.5em;
		text-align: left;
		width: 300px;
	}

	.work {
		left: 2em;
		top: 2em;
		align-items: flex-start;
	}
	.work .work-content {
		display: grid;
		grid-template-columns: 1fr;
		grid-gap: 5em;
	}
	.project img {
	width: auto;
		height: 45em;
	}

	.contact {
		top: 15em;
		left: 2em;
	}
  h1 {
		font-size: 2em;
	}
	.contact .email {
		font-size: 3.5em;
	}

	.contact li {
		display: block;
		padding: 2em;
	}
	.contact li a {
		font-size: 2em;
	}
	.footer {
		top: 4em;
		padding: 1em;
		font-size: 1em;
	}
}

/* iPads (portrait and landscape) ----------- */
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
	body {
		font-size: 1em;
	}
	header h1 {
		font-size: 5em;
		padding: 1em;
		margin-right: 0em;
	}
nav li .link {
    text-decoration: none;
    padding: 2em;
    opacity: 0.5;
    font-size: 2em;
}

	.header-title {
		font-size: 3.6em;
		text-align: left;
		top: 1em;
		left: 1em;
	}
	.about {
		position: relative;
		display: flex;
		top: -10em;
	}
	.about img {
		display: none;
	}
	.about-content {
		left: 6em;
	}
	.about-content p {
		font-size: 3.5em;
		text-align: left;
		width: 700px;
	}

	.work {
		left: 6em;
		top: 4em;
		align-items: flex-start;
	}
	.work .work-content {
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-gap: 5em;
	}
	.project img {
	width: auto;
		height: 30em;
	}

	.contact {
		top: 15em;
		left: 6em;
	}
	.contact h1 {
		font-size: 4em;
	}
	.contact .email {
		font-size: 2em;
	}

	.contact li {
		display: block;
		padding: 2em;
	}
	.contact li a {
		font-size: 2em;
	}
	.footer {
		top: 4em;
		padding: 1em;
		font-size: 2em;
	}
}

/* Large screens ----------- */
@media only screen and (min-width: 1824px) {
	/* Styles */

	body {
		font-size: 0.75em;
  }
  .App-header {
	position: relative;
	min-height: 100vh;
}
	header h1 {
		font-size: 3em;
		margin-right: 30em;
	}

	nav {
		left: 60em;
		top: 12em;
		z-index: 100;
	}
	nav li {
		font-size: 1.5em;
	}
	.header-title {
		font-size: 3em;
		top: 1em;
		left: 1em;
	}
	.about {
		position: relative;
		display: flex;
		top: -20em;
	}
	.about-content {
		left: 10em;
	}
	.about-content p {
		font-size: 3.5em;
		text-align: left;
		width: 25em;
	}
	.about img {
		margin-left: 1em;
	}

	.work {
		left: 10em;
		top: 4em;
		align-items: flex-start;
	}
	.work .work-content {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
	}
	.project img {
	width: auto;
		height: 32em;
	}

	.contact {
		top: 8em;
		left: 8em;
	}
	.contact h1 {
		font-size: 5em;
	}
	.contact .email {
		font-size: 3em;
	}

	.contact li a {
		font-size: 3em;
		padding: 0em;
	}
	.footer {
		top: 4em;
		padding: 1em;
		font-size: 3em;
	}
}

